import React, { useEffect, useRef } from 'react'
import gsap from 'gsap'

const TitleAnimate = ({ text, class_names }) => {

    const wordRefs = useRef([])

    useEffect(() => {
        gsap.to(wordRefs.current, {
            y: 0,
            stagger: 0.1,
            delay: 0.2,
            ease: 'power2.out',
            duration: 0.5,
        })
    }, [])

    return (
        <h1 className={`${class_names || ''} montserrat-bold`}>
            {text.split(' ').map((word, i) => (
                <span className='mask' key={i}>
                    <span
                        className='word'
                        ref={el => (wordRefs.current[i] = el)}
                    >
                        {word}
                    </span>
                </span>
            ))}
        </h1>
    )
}

export default TitleAnimate