import './index.css'
import 'bootstrap/dist/css/bootstrap.min.css'
import React, { useEffect, useRef } from 'react'

import gsap from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'

import { SwimmingData, WaterpoloData } from '../../utils/swimming_data'

import { BackButton, CoverPhoto, TitleAnimate, ParagraphAnimate, ArticleSection, ImageAnimate } from '../../utils/article_components'

import swimming_vid_1 from '../../videos/Swimming.mp4'
import swimming_vid_2 from '../../videos/Swimming 2.mp4'
import swim_deco from '../../img/swim-deco-1.png'

import swimming_1 from '../../img/swimming_1.png'
import swimming_2 from '../../img/swimming_2.png'
import swimming_3 from '../../img/swimming_3.jpg'
import swimming_4 from '../../img/swimming_4.jpg'
import swimming_5 from '../../img/swimming_5.jpg'

import waterpolo_1 from '../../img/waterpolo_1.png'
import waterpolo_2 from '../../img/waterpolo_2.jpg'
import waterpolo_3 from '../../img/waterpolo_3.jpg'

gsap.registerPlugin(ScrollTrigger)

export const Swimming = () => {

    return (
        <div className='swimming'>
            <CoverPhoto cover_photo={SwimmingData.cover_photo} color={SwimmingData.color} opacity={0.5}/>
            <BackButton color={SwimmingData.color} />
            <div className='section-content d-flex flex-column w-100'>
                <div className='fake-bg position-absolute w-100' />
                <TitleAnimate text={SwimmingData.title} class_names={'section-title'} />
                <div className='swimming-intro section-block d-flex flex-column align-items-center justify-content-center mx-custom mt-4'>
                    <div className='media-container w-100 d-flex align-items-center justify-content-center'>
                        <video src={swimming_vid_1} autoPlay='true' muted='true' controls='controls' className='swimming-media'/>
                        {
                            Array.from({ length: 4 }, (_, i) => (
                                <SwimmingDecoration class_name={`deco deco-${i+1}`} delay={i/15}/>
                            ))
                        }
                    </div>

                    <h1 className='montserrat-bold heading'>Swimming: A Lifelong Journey</h1>
                    <ParagraphAnimate text={SwimmingData.swimming_story_1}/>

                    <div className='story_1_collage heading d-flex align-items-center justify-content-between'>
                        <div className='img_wrapper d-flex justify-content-center'>
                            <ImageAnimate src={swimming_1} alt={'My Child Swimming Picture'} class_names={'img-1 swimming-media'}/>
                            {/* <SwimmingDecoration class_name={'deco deco-5'} delay={0}/>
                            <SwimmingDecoration class_name={'deco deco-6'} delay={0}/> */}
                        </div>
                        <div className='img_wrapper d-flex justify-content-center'>
                            <ImageAnimate src={swimming_2} alt={'My Child Swimming Picture'} class_names={'img-1 swimming-media'}/>
                            {/* <SwimmingDecoration class_name={'deco deco-7'} delay={0}/>
                            <SwimmingDecoration class_name={'deco deco-8'} delay={0}/> */}
                        </div>
                    </div>

                    <ParagraphAnimate text={SwimmingData.swimming_story_2}/>

                    <div className='media-container m-extra w-100 d-flex align-items-center justify-content-center'>
                        <video src={swimming_vid_2} autoPlay='true' muted='true' controls='controls' className='swimming-media w-90'/>
                        {
                            Array.from({ length: 4 }, (_, i) => (
                                <SwimmingDecoration class_name={`deco deco-${i+1}`} delay={i/20}/>
                            ))
                        }
                    </div>

                    <ParagraphAnimate text={SwimmingData.swimming_story_3}/>

                    <div className='story_2_collage heading d-flex align-items-center justify-content-between'>
                        <div className='img_wrapper d-flex justify-content-center'>
                            <ImageAnimate src={swimming_3} alt={'My Professional Swimming Picture'} class_names={'img-2 swimming-media'}/>
                        </div>
                        <div className='img_wrapper d-flex flex-column align-items-center align-self-stretch justify-content-around'>
                            <ImageAnimate src={swimming_4} alt={'My Professional Swimming Picture'} class_names={'img-3 swimming-media'}/>
                            <ImageAnimate src={swimming_5} alt={'My Professional Swimming Picture'} class_names={'img-3 swimming-media'}/>
                        </div>
                    </div>

                    <ParagraphAnimate text={SwimmingData.swimming_story_4}/>
                </div>

                <div className='background-quote d-flex align-items-center justify-content-center w-100'>
                    <ParagraphAnimate text={SwimmingData.quote} class_names={'montserrat-bold w-75'} start={'top 60%'} stagger={0.009}/>
                </div>

                <div className='waterpolo-intro section-block d-flex flex-column align-items-center justify-content-center mx-custom mt-4'>
                    <h1 className='montserrat-bold heading'>Water Polo: The Game That Stole My Heart</h1>
                    <ParagraphAnimate text={WaterpoloData.waterpolo_story_1}/>

                    <div className='story_3_collage heading d-flex align-items-center justify-content-between'>
                        <div className='img_wrapper d-flex justify-content-center'>
                            <ImageAnimate src={waterpolo_1} alt={'My Child Waterpolo Picture'} class_names={'img-4 swimming-media'}/>
                        </div>
                        <div className='img_wrapper d-flex flex-column align-items-center justify-content-around'>
                            <ImageAnimate src={waterpolo_2} alt={'My Professional Waterpolo Picture'} class_names={'img-5 swimming-media'}/>
                        </div>
                    </div>

                    <ParagraphAnimate text={WaterpoloData.waterpolo_story_2}/>

                    <div className='media-container m-extra w-100 d-flex align-items-center justify-content-center'>
                        <ImageAnimate src={waterpolo_3} alt={'My Professional Waterpolo Picture'} class_names={'swimming-media w-90'}/>
                        {
                            Array.from({ length: 4 }, (_, i) => (
                                <SwimmingDecoration class_name={`deco deco-${i+1}`} delay={i/15}/>
                            ))
                        }
                    </div>

                    <ParagraphAnimate text={WaterpoloData.waterpolo_story_3}/>
                </div>
                <ArticleSection excludeSection={3} text={'Please explore other sections about me'} color={SwimmingData.color} class_names={'mb-10vh'}/>
            </div>
        </div>
    )
}

function SwimmingDecoration({class_name, delay}) {

    const decoRef = useRef(null)

    useEffect(() => {
        gsap.to(decoRef.current, 0.5, {
            x: 0,
            y: 0,
            delay: delay,
        })
    }, [delay])

    return (
        <React.Fragment>
            <img src={swim_deco} alt='Swimming Decoration' className={class_name} ref={decoRef}/>
        </React.Fragment>
    )
}