import React, { useEffect, useRef } from 'react'
import gsap from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'
gsap.registerPlugin(ScrollTrigger)

const ImageAnimate = ({ src, alt, class_names }) => {

    const blurAnim = useRef(null)

    useEffect(() => {
        gsap.to(blurAnim.current, 0.5, {
            xPercent: 110,
            scrollTrigger: {
                trigger: blurAnim.current,
                start: '0% 90%',
                end: '0% 0%',
                once: true
            }
        })
    }, [])

    return ( 
        <div className={`${class_names || ''}`}>
            <img src={src} alt={alt}/>
            <div className='blur-anim w-100 h-100' ref={blurAnim}/>
        </div>
    )
}

export default ImageAnimate