    methane_story_1: "I am currently competing in the Methane Abatement Challenge with my team, \"Triple Bond,\" where we are tackling the critical issue of methane leakage. In this competition, we're up against both schools and professionals from across the UAE, working to develop innovative solutions to reduce methane emissions.",
