    football_story: 'Although swimming has always been my primary sport, my athletic background pushed me to give football a tryand it paid off. I not only made the varsity team but also became the team captain. Leading my team to three victories in the ADEK Cup in the UAE was a huge accomplishment. The ADEK Cup is one of the most competitive school-level tournaments in the country, so winning multiple matches wasnt just a testament to our hard work, but also to my leadership on and off the field. Being able to guide my team through such an intense competition was an unforgettable experience, proving that the skills Ive developed as an athlete go beyond the swimming pool.',
