import React, { useEffect, useRef } from 'react'
import gsap from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'
gsap.registerPlugin(ScrollTrigger)

const ParagraphAnimate = ({ text, class_names, start='top 90%', stagger=0.005 }) => {

    const paragraphRef = useRef(null)

    useEffect(() => {
        gsap.to(paragraphRef.current.querySelectorAll('.word'), 0.2, {
            y: 0,
            opacity: 1,
            stagger: stagger,
            ease: "power2.out",
            scrollTrigger: {
                trigger: paragraphRef.current,
                start: start,
                toggleActions: "play none none reverse",
                once: true,
            }
        })
    }, [text, start, stagger])

    return (
        <p className={`${class_names || ''} raleway-regular`} ref={paragraphRef}>
            {
                text.split('//').map((part, i) => (
                    <React.Fragment>
                        {
                            part.split(' ').map((word, j) => (
                                <span className='mask' key={j}>
                                    <span className='word' key={j}>
                                        {word.startsWith('$') ? <b>{word.replace('$', '')}</b> : word}
                                    </span>
                                </span>
                            ))
                        }
                    {i < text.split('//').length - 1 && <React.Fragment><br/><br/></React.Fragment>}
                    </React.Fragment>
                ))
            }
        </p>
    )
}

export default ParagraphAnimate