import './index.css'
import 'bootstrap/dist/css/bootstrap.min.css'
import React, { useState, useRef } from 'react'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faQuoteLeft } from '@fortawesome/free-solid-svg-icons'

import { RecommendationData, Testimonials, MainTestimonial } from '../../utils/recommendations_data'

import { BackButton, ButtonAnimate, CoverPhoto, TitleAnimate, ParagraphAnimate, ArticleSection } from '../../utils/article_components'

export const Recommendations = () => {

  return (
    <div className='recommendations'>
      <CoverPhoto cover_photo={RecommendationData.cover_photo} color={RecommendationData.color} />
      <BackButton color={RecommendationData.color} />
      <div className='section-content d-flex flex-column w-100'>
        <div className='fake-bg position-absolute w-100' />
        <TitleAnimate text={RecommendationData.title} class_names={'section-title'} />
        <div className='recommendations-intro section-block d-flex flex-column align-items-center justify-content-center mx-custom mt-4'>
          <div className='testimonial-main'>
            <Testimonial data={MainTestimonial} />
          </div>
          <ParagraphAnimate text={RecommendationData.introduction} class_names={'p-heading text-center'}/>
          <div className='testimonial-container'>
            <div className='testimonial-left'>
            {
              Testimonials.map((data, i) => {
                if (i % 2 === 0) {
                  return (
                    <Testimonial data={data} key={i}/>
                  )
                }

                return null
              })
            }
            </div>
            <div className='testimonial-right'>
            {
              Testimonials.map((data, i) => {
                if (i % 2 !== 0) {
                  return (
                    <Testimonial data={data} key={i}/>
                  )
                }

                return null
              })
            }
            </div>
            <div className='testimonial-middle'>
            {
              Testimonials.map((data, i) => {
                return (
                  <Testimonial data={data} key={i}/>
                )
              })
            }
            </div>
          </div>
        </div>
        <ArticleSection excludeSection={1} text={'Please explore other sections about me'} color={RecommendationData.color} class_names={'mb-10vh'}/>
      </div>
    </div>
  )
}

function Testimonial({data, key}) {
  const [full, setFull] = useState(false)
  const testimonialRef = useRef(null)

  const scrollToTestimonial = () => {
      testimonialRef.current.scrollIntoView({
          behavior: 'smooth',
          block: 'start',
      })
  }

  return (
    <div className='testimonial w-100' key={key} ref={testimonialRef}>
      <div className='testimonial-header d-flex align-items-end'>
        <FontAwesomeIcon icon={faQuoteLeft}/>
        <div className='testimonial-person text-end'>
          <h3 className='montserrat-bold'>{data.name}</h3>
          <h5 className='raleway-regular'>{data.description}</h5>
        </div>
      </div>
      <FontAwesomeIcon icon={faQuoteLeft}/>
      <ParagraphAnimate text={full ? data.full_testimonial : data.partial_testimonial}/>
      <ButtonAnimate text={full ? 'Hide Full Message' : 'Show Full Message'} class_names={'testimonial-btn full-btn w-75'} 
                      on_click={() => {setFull(!full); scrollToTestimonial()}} stagger={0.02}/>
      <ButtonAnimate text={'Download Original Document'} class_names={'testimonial-btn download-btn w-75'} 
                      href={data.document} download={data.document_name} stagger={0.02}/>
    </div>
  )
}