import './index.css'
import 'bootstrap/dist/css/bootstrap.min.css'
import React, { useEffect, useRef } from 'react'

import gsap from 'gsap'
import { useGSAP } from '@gsap/react'
import { ScrollTrigger } from 'gsap/ScrollTrigger'

import { AcademicData, AcademicAwards, GradeData } from '../../utils/academic_data'
import LineChart from '../../components/linechart'

import { BackButton, ButtonAnimate, CoverPhoto, TitleAnimate, ParagraphAnimate, ImageAnimate, ArticleSection } from '../../utils/article_components'

import trophy from '../../img/trophy.png'
import gcse from '../../img/gcse.jpg'
import mun_award from '../../img/mun_award.png'
import mun_group_photo from '../../img/mun_group_photo.jpg'

gsap.registerPlugin(useGSAP)
gsap.registerPlugin(ScrollTrigger)

export const Academic = () => {

    // ON SCROLL

    useEffect(() => {
        gsap.fromTo('.section-card', 0.75, {
            yPercent: -100,
            opacity: 0,
        }, {
            yPercent: 0,
            opacity: 1,
            stagger: 0.1,
            scrollTrigger: {
                trigger: '.section-card',
                start: '0% 20%',
                end: '0% 0%',
                once: true
            }
        })

        gsap.to('.gcse .blur-anim', 0.5, {
            xPercent: 110,
            scrollTrigger: {
                trigger: '.gcse .blur-anim',
                start: '0% 90%',
                end: '0% 0%',
                once: true
            }
        })
    }, [])


    return (
        <div className='academic'>
            <BackButton color={AcademicData.color}/>
            <CoverPhoto cover_photo={AcademicData.cover_photo} color={AcademicData.color}/>
            
            <div className='section-content d-flex flex-column w-100'>
                <div className='fake-bg position-absolute w-100'/>
                <TitleAnimate text={AcademicData.title} class_names={'section-title'}/>
                <div className='academic-intro section-block d-flex flex-column align-items-center justify-content-center mx-custom mt-4'>
                    <div className='large-chart py-custom'>
                        <LineChart color={AcademicData.color} title={'GPA ALONG THE YEARS'}/>
                    </div>
                    <h1 className='montserrat-bold heading'>
                        My Journey: A Path to Academic Excellence
                    </h1>
                    <ParagraphAnimate text={AcademicData.my_story}/>
                </div>

                <h4 className='heading-outside raleway-regular mx-custom'>
                    Here are my academic transcripts for the past three years, showcasing the steady improvement in my performance...
                </h4>

                <div className='section-card-container d-flex align-items-start justify-content-between mx-custom mt-4'>
                    {
                        GradeData.map((grade, i) => {
                            return (
                                <SectionCard 
                                    i={i}
                                    data={grade} />
                            )
                        })
                    }
                </div>

                <div className='gcse section-block d-flex flex-column align-items-center mx-custom'>
                    <h1 className='montserrat-bold heading'>My GCSE Journey: From Struggle to Triumph</h1>
                    <ParagraphAnimate text={AcademicData.gcse_story}/>
                    <ImageAnimate class_names={'full-width-photo'} src={gcse} alt={'GSCE Image with my friend'}/>                    
                </div>

                <div className='a-level section-block d-flex flex-column align-items-center mx-custom mb-5'>
                    <h1 className='montserrat-bold heading'>A-Level Mastery: Building on Success</h1>
                    <ParagraphAnimate text={AcademicData.a_level_story}/>
                    <h1 className='montserrat-bold heading'>MUN: Model United Nations</h1>
                    <ParagraphAnimate text={AcademicData.mun_1_story}/>
                    <div className='mun-photo-container d-flex justify-content-between'>
                        <ImageAnimate class_names={'mun-photo'} src={mun_award} alt={'MUN Award'}/>
                        <ImageAnimate class_names={'mun-photo'} src={mun_group_photo} alt={'MUN Group Photo'}/>
                    </div>
                    <ParagraphAnimate text={AcademicData.mun_2_story}/>
                    <div className='mun-award-container d-flex flex-wrap justify-content-between'>
                        {
                            AcademicAwards.map((award, i) => (
                                <div className='d-flex flex-column align-items-center' key={i}>
                                    <ImageAnimate class_names={'mun-award-photo'} src={award.img} alt={award.name}/>
                                    <p className='raleway-regular mun-award-name'>Award: {award.name}</p>
                                </div>
                            ))
                        }
                    </div>
                    
                </div>
                <ArticleSection excludeSection={0} text={'Please explore other sections about me'} color={AcademicData.color} class_names={'mb-10vh'}/>
            </div>
        </div>
    )
}

function SectionCard({i, data}) {
    const DownloadBtnAnimation = useRef([]);

    useEffect(() => {
        DownloadBtnAnimation.current = GradeData.map((grade, i) => {
            const tl = gsap.timeline({paused: true});
            
            tl.to(`.download-btn.btn-${i} .mask .letter`, 0.3, {
                yPercent: -100,
                stagger: 0.03,
                ease: 'power2.out'
            })
    
            tl.to(`.download-btn.btn-${i} .mask .hidden-letter`, 0.3, {
                yPercent: -100,
                stagger: 0.03,
                ease: 'power2.out'
            }, '<')
    
            return tl
        })
    }, [])

    return (
        <div className='section-card-mask'>
            <div className='section-card'>
                {
                    i === 2 && 
                    <div className='with-high-honors raleway-regular d-flex flex-wrap align-items-center'>
                        <img src={trophy} alt='Trophy Icon'/>
                        <p>With High Honors!</p>
                    </div>
                }
                <div className='section-card-header d-flex align-items-end justify-content-around'>
                    <div className='left'>
                        <h5 className='raleway-regular'>{data.grade}</h5>
                        <div className='d-flex'>
                            <h1 className='montserrat-bold'>{data.gpa}</h1>
                        </div>  
                    </div>
                    <div className='right d-flex justify-content-end'>
                        <h4 className='raleway-regular'>{data.year}</h4>
                    </div>
                </div>
                <div className='section-card-content d-flex flex-column'>
                    <img src={data.img} alt='Grade Transcript'/>
                    <ButtonAnimate 
                        text={'Download PDF'} 
                        color={'#FFFFFF'} 
                        class_names={`download-btn btn-${i} w-75`}
                        href={data.pdf}
                        download={data.pdf_name} />
                </div>
            </div>
        </div>
    )
}