import './index.css'
import 'bootstrap/dist/css/bootstrap.min.css'
import React, { useState, useRef } from 'react'

import { WorkExperienceData } from '../../utils/work_experiences_data'

import { BackButton, CoverPhoto, TitleAnimate, ParagraphAnimate, ArticleSection, ImageAnimate, ButtonAnimate } from '../../utils/article_components'

import leadership from '../../img/leadership.png'
import committee from '../../img/committee.jpg'

export const WorkExperience = () => {
    return (
        <div className='experiences'>
            <CoverPhoto cover_photo={WorkExperienceData.cover_photo} color={WorkExperienceData.color} opacity={0.5}/>
            <BackButton color={WorkExperienceData.color} />
            <div className='section-content d-flex flex-column w-100'>
                <div className='fake-bg position-absolute w-100' />
                <TitleAnimate text={WorkExperienceData.title} class_names={'section-title'} />
                <div className='section-block d-flex flex-column align-items-center justify-content-center mx-custom mt-4'>
                    <h1 className='montserrat-bold heading'>{WorkExperienceData.experience_1_title}</h1>
                    <ParagraphAnimate text={WorkExperienceData.experience_1_story_1}/>
                    <ImageAnimate class_names={'half-width-photo'} src={leadership} alt={'Leadership Public Speaking'}/>
                    <ParagraphAnimate text={WorkExperienceData.experience_1_story_2}/>
                </div>

                <div className='section-block d-flex flex-column align-items-center justify-content-center mx-custom mt-4'>
                    <h1 className='montserrat-bold heading'>{WorkExperienceData.experience_2_title}</h1>
                    <ParagraphAnimate text={WorkExperienceData.experience_2_story_1}/>
                    <ImageAnimate class_names={'half-width-photo'} src={committee} alt={'Committee Group'}/>
                    <ParagraphAnimate text={WorkExperienceData.experience_2_story_2}/>
                </div>

                <ArticleSection excludeSection={4} text={'Please explore other sections about me'} color={WorkExperienceData.color} class_names={'mb-10vh'}/>
            </div>
        </div>
    )
}