import './index.css'
import 'bootstrap/dist/css/bootstrap.min.css'
import React from 'react'

import { FutureGoalsData } from '../../utils/future_goals_data'

import { BackButton, CoverPhoto, TitleAnimate, ParagraphAnimate, ArticleSection } from '../../utils/article_components'

export const FutureGoals = () => {
    return (
        <div className='future-goals'>
            <CoverPhoto cover_photo={FutureGoalsData.cover_photo} color={FutureGoalsData.color} opacity={0.6}/>
            <BackButton color={FutureGoalsData.color} />
            <div className='section-content d-flex flex-column w-100'>
                <div className='fake-bg position-absolute w-100' />
                <TitleAnimate text={FutureGoalsData.title} class_names={'section-title'} />
                <div className='section-block d-flex flex-column align-items-center justify-content-center mx-custom mt-4'>
                    <h1 className='montserrat-bold heading'>{FutureGoalsData.goal_1_title}</h1>
                    <ParagraphAnimate text={FutureGoalsData.goal_1_content}/>
                </div>

                <div className='section-block d-flex flex-column align-items-center justify-content-center mx-custom mt-4'>
                    <h1 className='montserrat-bold heading'>{FutureGoalsData.goal_2_title}</h1>
                    <ParagraphAnimate text={FutureGoalsData.goal_2_content}/>
                </div>

                <div className='section-block d-flex flex-column align-items-center justify-content-center mx-custom mt-4'>
                    <h1 className='montserrat-bold heading'>{FutureGoalsData.goal_3_title}</h1>
                    <ParagraphAnimate text={FutureGoalsData.goal_3_content}/>
                </div>

                <div className='section-block d-flex flex-column align-items-center justify-content-center mx-custom mt-4'>
                    <h1 className='montserrat-bold heading'>{FutureGoalsData.goal_4_title}</h1>
                    <ParagraphAnimate text={FutureGoalsData.goal_4_content}/>
                </div>

                <ArticleSection excludeSection={5} text={'Please explore other sections about me'} color={FutureGoalsData.color} class_names={'mb-10vh'}/>
            </div>
        </div>
    )
}