import './cover_photo.css'
import 'bootstrap/dist/css/bootstrap.min.css'
import React, { useState, useEffect, useRef } from 'react'

const CoverPhoto = ({cover_photo, color, opacity, vh, trigger_height_multiplier}) => {
    const [isVisible, setIsVisible] = useState(true)
    const CoverPhoto = useRef(null)

    useEffect(() => {
        const handleScroll = () => {
          const scrollPosition = window.scrollY;
          const triggerHeight = window.innerHeight * (trigger_height_multiplier ? trigger_height_multiplier : 1);
    
          if (scrollPosition > triggerHeight) {
            setIsVisible(false);
          } else {
            setIsVisible(true);
          }
        };
    
        window.addEventListener('scroll', handleScroll);
    
        return () => {
          window.removeEventListener('scroll', handleScroll);
        }
        
    }, [trigger_height_multiplier])

    useEffect(() => {
      if (color && CoverPhoto.current) {
        CoverPhoto.current.style.setProperty('--cover-filter', color)
        CoverPhoto.current.style.setProperty('--cover-filter-opacity', opacity ? opacity : 0.75)
      }
    }, [color, opacity]);

    return (
        <div className={`cover-photo ${color ? 'section-cover-photo' : ''} position-fixed top-0 start-0 vw-100 ${vh ? vh : 'vh-80'}`} 
            style={{ 
              background: `url(${cover_photo}) no-repeat`,
              display: isVisible ? 'block' : 'none'
            }}
            ref={CoverPhoto}
        />
    )
}

export default CoverPhoto