import React, { useEffect, useRef } from 'react'
import gsap from 'gsap'
import { useGSAP } from '@gsap/react'
import './button_animate.css'

gsap.registerPlugin(useGSAP)

const ButtonAnimate = ({text, color, class_names, on_click, href, download, stagger = 0.03, duration = 0.3, triggerAnim = null}) => {

    const Button = useRef(null)
    const Letters = useRef([])
    const HiddenLetters = useRef([])
    const BackAnimation = useRef(null)

    useEffect(() => {
        Button.current.style.setProperty('--button-color', color)

        gsap.to(Button.current, 0.2, {
            y: 0,
            delay: 0.1,
            ease: "power2.out"
        })
    }, [color])

    // ON HOVER

    useEffect(() => {

        const tl_1 = gsap.timeline({paused: true})
    
        tl_1.to(Letters.current, duration, {
            yPercent: -100,
            stagger: stagger,
            ease: 'power2.out'
        })
    
        tl_1.to(HiddenLetters.current, duration, {
            yPercent: -100,
            stagger: stagger,
            ease: 'power2.out'
        }, '<')
    
        BackAnimation.current = tl_1
        
        }, [stagger, duration])

    useEffect(() => {
        if (triggerAnim === 'enter') {
            BackAnimation.current.play();
        } else if (triggerAnim === 'leave') {
            BackAnimation.current.reverse();
        }
    }, [triggerAnim])

    function onAnimEnter(animation) {

        if (animation && triggerAnim == null) {
            animation.play()
        }
    }
    
    function onAnimLeave(animation) {

        if (animation && triggerAnim == null) {
            animation.reverse()
        }
    }

    return (
        <a className={`raleway-regular ${class_names || ''}`}
            ref={Button}
            onMouseEnter={() => onAnimEnter(BackAnimation.current)}
            onMouseLeave={() => onAnimLeave(BackAnimation.current)}
            onClick={on_click}
            href={href}
            download={download}>
            {
                text.split(' ').map((word, i) => (
                    <span className='d-inline-flex'>
                        {
                            word.split('').map((letter, i) => (
                                <span className='mask'>
                                    <span className='letter' key={i} ref={el => (Letters.current.push(el))}>
                                        {letter}
                                    </span>
                                    <span className='hidden-letter' key={i} ref={el => (HiddenLetters.current.push(el))}>
                                        {letter}
                                    </span>
                                </span>
                            ))
                        }
                        <span className='space-right'></span>
                    </span>
                ))
            }
        </a>
    )
}

export default ButtonAnimate