import { useNavigate } from "react-router-dom"
import React, { useEffect, useRef } from 'react'
import gsap from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'
import { Sections } from "../utils/home_data"

gsap.registerPlugin(ScrollTrigger)

const ArticleSection = ({excludeSection, text, color, class_names}) => {
    return(
        <div className={`section-selection py-custom box-custom mx-custom ${class_names}`}>
          <h5 className='raleway-regular'>{text}</h5>
          <div className='sections-container'>
            {
                Sections.reduce((rows, section, i) => {
                    if (i === excludeSection) return rows;

                    const rowIndex = Math.floor(i / 3);

                    if (!rows[rowIndex]) rows[rowIndex] = [];

                    rows[rowIndex].push(
                    <Section data={section} color={color} sectionKey={i+1} key={i} />
                    );

                    return rows;
                }, []).map((row, index) => (
                    <div className="row" key={index}>
                    {row}
                    </div>
                ))
            }
          </div>
        </div>
    )
}

function Section({data, sectionKey, color}){
    const Navigate = useNavigate()
    const sectionRef = useRef(null)

    useEffect (() => {
        gsap.fromTo(sectionRef.current, 0.2, {
            yPercent: -100,
            opacity: 0,
          }, {
            yPercent: 0,
            opacity: 1,
            ease: "power2.out",
            scrollTrigger: {
                trigger: sectionRef.current,
                start: '-30% 100%',
                once: true,
            }
          }, '<')

        sectionRef.current.style.setProperty('--sections-color', color)
        sectionRef.current.style.setProperty('--sections-wrap-color', `${color}AA`)
    }, [color])
    
    return(
      <div className='section-mask col m-custom px-0' onClick={() => Navigate(data.link)}>
        <div className={`section section-${sectionKey} d-flex align-items-end`} ref={sectionRef}
            style={{
                background: `url(${data.img}) no-repeat center center`
              }}>
          <div className='section-text-wrapper d-flex align-items-center'>
            <h1 className='raleway-regular text-white'>{data.title}</h1>
          </div>
        </div>
      </div>
    );
}

export default ArticleSection