import './index.css'
import 'bootstrap/dist/css/bootstrap.min.css'
import React, { useState, useRef } from 'react'

import gsap from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'

import { ExtracurricularData, ExtracurricularContents } from "../../utils/extracurricular_data"

import { BackButton, CoverPhoto, TitleAnimate, ParagraphAnimate, ArticleSection, ImageAnimate, ButtonAnimate } from '../../utils/article_components'

import acting_1 from '../../img/acting_1.jpg'
import acting_2 from '../../img/acting_2.jpg'
import acting_3 from '../../img/acting_3.jpg'
import academy_excellence from '../../img/academy_excellence.jpg'
import methane from '../../img/methane.jpg'
import mun from '../../img/mun_group_photo.jpg'
import public_speaking from '../../img/public_speaking.png'
import tutoring from '../../img/tutoring_others.jpg'
import football_varsity from '../../img/football_varsity.jpg'
import basketball_varsity from '../../img/basketball_varsity.jpg'
import volunteering from '../../img/volunteering.jpg'

gsap.registerPlugin(ScrollTrigger)

export const Extracurricular = () => {

    const contentsRef = useRef(ExtracurricularContents.map(() => React.createRef()))


    return (
        <div className='extracurricular'>
            <CoverPhoto cover_photo={ExtracurricularData.cover_photo} color={ExtracurricularData.color} opacity={0.6}/>
            <BackButton color={ExtracurricularData.color} />
            <div className='section-content d-flex flex-column w-100'>
                <div className='fake-bg position-absolute w-100' />
                <TitleAnimate text={ExtracurricularData.title} class_names={'section-title'} />
                <div className='extracurricular-toc section-block d-flex align-items-center justify-content-center mx-custom mt-4'>
                    <div className='left'>
                        {
                            ExtracurricularContents.slice(0, 5).map((content, i) => (
                                <TableOfContent key={i} number={(i+1).toString()} text={content} target={contentsRef.current[i]}/>
                            ))
                        }
                    </div>
                    <div className='right'>
                        {
                            ExtracurricularContents.slice(5).map((content, i) => (
                                <TableOfContent key={i+5} number={(i+6).toString()} text={content} target={contentsRef.current[i+5]}/>
                            ))
                        }
                    </div>
                </div>

                <div ref={contentsRef.current[0]} className='section-block d-flex flex-column align-items-center justify-content-center mx-custom mt-4'>
                    <h1 className='montserrat-bold heading'>{ExtracurricularContents[0]}</h1>
                    <ParagraphAnimate text={ExtracurricularData.acting_story_1}/>
                    <div className='extracurricular-photo-container d-flex justify-content-between'>
                        <ImageAnimate class_names={'extracurricular-photo'} src={acting_1} alt={'Acting 1'}/>
                        <ImageAnimate class_names={'extracurricular-photo'} src={acting_2} alt={'Acting 2'}/>
                    </div>
                    <ParagraphAnimate text={ExtracurricularData.acting_story_2}/>
                    <ImageAnimate class_names={'full-width-photo'} src={acting_3} alt={'Acting 3'}/>
                </div>

                <div ref={contentsRef.current[1]} className='section-block d-flex flex-column align-items-center justify-content-center mx-custom mt-4'>
                    <h1 className='montserrat-bold heading'>{ExtracurricularContents[1]}</h1>
                    <ParagraphAnimate text={ExtracurricularData.academic_story_1}/>
                    <ImageAnimate class_names={'half-width-photo'} src={academy_excellence} alt={'Academic Excellence'}/>
                    <ParagraphAnimate text={ExtracurricularData.academic_story_2}/>
                </div>

                <div ref={contentsRef.current[2]} className='section-block d-flex flex-column align-items-center justify-content-center mx-custom mt-4'>
                    <h1 className='montserrat-bold heading'>{ExtracurricularContents[2]}</h1>
                    <ParagraphAnimate text={ExtracurricularData.methane_story_1}/>
                    <ImageAnimate class_names={'half-width-photo'} src={methane} alt={'Methane Challenge'}/>
                    <ParagraphAnimate text={ExtracurricularData.methane_story_2}/>
                </div>

                <div ref={contentsRef.current[3]} className='section-block d-flex flex-column align-items-center justify-content-center mx-custom mt-4'>
                    <h1 className='montserrat-bold heading'>{ExtracurricularContents[3]}</h1>
                    <ParagraphAnimate text={ExtracurricularData.mun_story_1}/>
                    <ImageAnimate class_names={'full-width-photo custom-mb'} src={mun} alt={'Model United Nations Group'}/>
                    <ParagraphAnimate text={ExtracurricularData.mun_story_2}/>
                </div>

                <div ref={contentsRef.current[4]} className='section-block d-flex flex-column align-items-center justify-content-center mx-custom mt-4'>
                    <h1 className='montserrat-bold heading'>{ExtracurricularContents[4]}</h1>
                    <ParagraphAnimate text={ExtracurricularData.public_speaking_1}/>
                    <ImageAnimate class_names={'half-width-photo custom-mb'} src={public_speaking} alt={'My Public Speaking'}/>
                    <ParagraphAnimate text={ExtracurricularData.public_speaking_2}/>
                </div>

                <div ref={contentsRef.current[5]} className='section-block d-flex flex-column align-items-center justify-content-center mx-custom mt-4'>
                    <h1 className='montserrat-bold heading'>{ExtracurricularContents[5]}</h1>
                    <ParagraphAnimate text={ExtracurricularData.tutoring_others}/>
                    <ImageAnimate class_names={'full-width-photo custom-mb'} src={tutoring} alt={'Me tutoring others'}/>
                </div>

                <div ref={contentsRef.current[6]} className='section-block d-flex flex-column align-items-center justify-content-center mx-custom mt-4'>
                    <h1 className='montserrat-bold heading'>{ExtracurricularContents[6]}</h1>
                    <ParagraphAnimate text={ExtracurricularData.football_story}/>
                    <ImageAnimate class_names={'full-width-photo custom-mb'} src={football_varsity} alt={'Football Team'}/>
                </div>

                <div ref={contentsRef.current[7]} className='section-block d-flex flex-column align-items-center justify-content-center mx-custom mt-4'>
                    <h1 className='montserrat-bold heading'>{ExtracurricularContents[7]}</h1>
                    <ParagraphAnimate text={ExtracurricularData.basketball_story_1}/>
                    <ImageAnimate class_names={'full-width-photo custom-mb'} src={basketball_varsity} alt={'Basketball Team'}/>
                    <ParagraphAnimate text={ExtracurricularData.basketball_story_2}/>
                </div>

                <div ref={contentsRef.current[8]} className='section-block d-flex flex-column align-items-center justify-content-center mx-custom mt-4'>
                    <h1 className='montserrat-bold heading'>{ExtracurricularContents[8]}</h1>
                    <ParagraphAnimate text={ExtracurricularData.bulgarian_school_1}/>
                </div>

                <div ref={contentsRef.current[9]} className='section-block d-flex flex-column align-items-center justify-content-center mx-custom mt-4'>
                    <h1 className='montserrat-bold heading'>{ExtracurricularContents[9]}</h1>
                    <ParagraphAnimate text={ExtracurricularData.volunteering_story_1}/>
                    <ImageAnimate class_names={'full-width-photo custom-mb'} src={volunteering} alt={'Volunteer Experience'}/>
                    <ParagraphAnimate text={ExtracurricularData.volunteering_story_2}/>
                </div>

                <ArticleSection excludeSection={2} text={'Please explore other sections about me'} color={ExtracurricularData.color} class_names={'mb-10vh'}/>
            </div>
        </div>
    )
}

function TableOfContent({number, text, target}) {

    const [hoverState, setHoverState] = useState(null)

    const scrollToContent = () => {
        target.current.scrollIntoView({
            behavior: 'smooth',
            block: 'start',
        })
    }

    return (
        <div 
            className='toc d-flex align-items-center justify-content-between'
            onMouseEnter={() => setHoverState('enter')}
            onMouseLeave={() => setHoverState('leave')}
            onClick={scrollToContent}
        >
            <ParagraphAnimate text={number.padStart(2, '0')} class_names={'number'}/>
            <ButtonAnimate text={text} class_names={'content-title'} triggerAnim={hoverState} duration={0.3}/>
        </div>
    )
}